import { writable, type Writable } from "svelte/store";
import type { ChartsSpec } from "../types/chart_spec";
import { fetchExperiments } from "./data_api_wrapper";

type PlotStyle = Record<string, Record<string, string|number>>

interface PlotterState {
    serverURL: string
    style: PlotStyle
    charts: Array<ChartsSpec>
}

const urlParams = new URLSearchParams(window.location.search);
const serverURL = decodeURIComponent(urlParams.get("serverURL") || ".")

const DefaultPlotterState: PlotterState = {
    serverURL,
    style: { // Global style
        "ensemble:first": {
            stroke: "#2E282A",
            "opacity": 0.35,
            "stroke-width": "2px",
        },
        "ensemble:last": {
            stroke: "#CD5334",
            "opacity": 0.35,
            "stroke-width": "2px",
        },
    },
    charts: [
        {
            kind: "summary",
            chart: "line",
            query: {
                ensembles: ["first", "last"],
                keyword: "FOPR",
                experiment: "auto"
            }
        },
        {   
            kind: "parameter",
            chart: "kdeAreas",
            query: {
                ensembles: ["first", "last"],
                keyword: "OP1_PERSISTENCE",
                experiment: "auto"
            },
            style: { // Global style
                "ensemble:first": {
                    stroke: "none",
                    "fill": "red",
                    "fill-opacity": 0.35,
                    "stroke-width": "2px",
                },
                "ensemble:last": {
                    stroke: "none",
                    "fill": "blue",
                    "fill-opacity": 0.35,
                    "stroke-width": "2px",
                },
            },
        },
    ]
}

const serializedState = decodeURIComponent(urlParams.get("serializedState") || "")

let useState: PlotterState
if (serializedState !== "") {
    useState = JSON.parse(serializedState) as PlotterState
} else {
    useState = DefaultPlotterState
}

export const ensureStoreIsSyncedWithExperiments = async () => {
    const experimentsMetadata = await fetchExperiments()
    const allExperiments = Object.keys(experimentsMetadata)
    useState.charts.forEach((spec) => {
        if (spec.query.experiment === "auto")
        spec.query.experiment = allExperiments[0]
    
        if (spec.kind === 'summary') {
            const availableKeywords = experimentsMetadata[spec.query.experiment].responses.summary.keys

            if (!availableKeywords.includes(spec.query.keyword)) {
                spec.query.keyword = availableKeywords[0]
            }
        } else if(spec.kind === 'parameter') {
            const paramMeta = experimentsMetadata[spec.query.experiment].parameters
            const availableParameters = [... 
                Object.entries(paramMeta)
                    .reduce((namesList, entry) => {
                        const [, param] = entry
                        const paramNames = param.transfer_function_definitions.map(d=>d.name)
                        return namesList.concat(paramNames)
                    }, [] as string[])
            ]
            
            if (!availableParameters.includes(spec.query.keyword)) {
                spec.query.keyword = availableParameters[0]
            }
        }
    })
}

export const plotterStore: Writable<PlotterState> = writable(useState)
