<script lang="ts">
    import { createEventDispatcher } from "svelte";
    import Icon from "./icons/Icon.svelte";
    import type { MouseEventHandler } from "svelte/elements";

    export let label: string = ""

    let chip: HTMLDivElement | undefined

    const dispatch = createEventDispatcher();
    const onDeleteClick: MouseEventHandler<HTMLButtonElement> = () => {
        dispatch("delete", { chip })
    }
</script>

<div class="flex items-center gap-1 min-h-7 rounded-full pl-3 chip" bind:this={chip} {...$$restProps}>
    <span class="chip__label">{label}</span>
    <button on:click={onDeleteClick} aria-label="Remove" class="flex items-center justify-center rounded-full chip__button">
        <Icon width="16" height="16" name="close" />
    </button>
</div>

<style lang="scss">
    .chip {
        background-color: #243746;
        color: #FFFFFF;
        padding-right: 2px;
    }

    .chip__label {
        font-size: 12px;
    }

    .chip__button {
        width: 24px;
        height: 24px;

        &:hover {
            background-color: #18252F;
        }
    }
</style>
