<script lang="ts">
	import SummaryLines from './charts/SummaryLines.svelte';
	import { getLoadedParameter } from './../stores/data_api_wrapper';
	import { type ParameterDataForRealization } from './../types/chart_spec.d';
	import TextInput from './input/TextInput.svelte';
	import { fetchExperiments, getLoadedExperiments, getLoadedSummary } from '../stores/data_api_wrapper';
	import { plotterStore } from '../stores/plotter_store';
    import type { ChartType, ParameterChartsSpec, SummaryChartsSpec, SummaryDataForRealization } from "../types/chart_spec";
    import { watchResize } from './gui-utils/watch-resize';
    import { fetchSummary, fetchParameter } from '../stores/data_api_wrapper';
    import Sidebar from './Sidebar.svelte';
    import Combobox from './input/Combobox.svelte';
    import ParameterKDEArea from './charts/ParameterKDEArea.svelte';

    // For summary we don't need a selector,
    // Maybe just pass a "*" or something like that
    export let axisMarginLeft: number = 35
    export let axisMarginBottom: number = 35
    export let showAxisX: boolean = true
    export let showAxisY: boolean = true
    
    export let spec: SummaryChartsSpec | ParameterChartsSpec

    const ensureExperimentIsSelected = async (): Promise<void> => {
        await fetchExperiments()
        const metadata = getLoadedExperiments()
        if (spec.query.experiment === "auto")
            spec.query.experiment = Object.keys(metadata)[0]
    }

    $: ensureComponentDataIsLoaded = async () => {
        const { kind, query } = spec
        switch (kind) {
            case 'summary':
                await fetchSummary(query)
                break
            case 'parameter':
                await fetchParameter(query)
                break
        }
    }

    $: computeEnsembleStyleMap = () => {
        const style = spec.style || {}
        const stylePerEnsemble: Record<string, Record<string, string>> = {}
        const experiment = getLoadedExperiments()[spec.query.experiment]

        experiment.eachEnsemble(
            (
                ensembleId: string, 
                ensembleAlias: string
            ) => {
                stylePerEnsemble[ensembleAlias] = stylePerEnsemble[ensembleId] = {}
                const globalStyle = $plotterStore.style
                Object.keys(globalStyle)
                    .filter(k => k.startsWith("ensemble:"))
                    .filter(k => k === ensembleAlias)
                    .forEach(k => Object.assign(stylePerEnsemble[ensembleAlias], globalStyle[k]))

                Object.keys(style || {})
                    .filter(k => k.startsWith("ensemble:"))
                    .filter(k => k === ensembleAlias)
                    .forEach(k => Object.assign(stylePerEnsemble[ensembleAlias], style![k]))
        })

        return stylePerEnsemble
    }
    
    $: performanceInfo = () => {
        const summary = findComponentAndDataInfo()
        const { timeSpentSeconds, MBProcessed } = summary
        return `Spent ${timeSpentSeconds.toFixed(4)} seconds to load a ${summary.data.length} realizations over ${MBProcessed}MB of files`
    }

    // Contains associations between
    // kinds of data, kinds of chart, and the actual
    // svelte component that renders the charts
    const DataToComponentType: { 
        kind: 'parameter' | 'summary', 
        chart: ChartType,
        component: any
    }[] = [
        {
            kind: 'parameter', 
            chart: 'kdeAreas', 
            component: ParameterKDEArea,
        },
        {
            kind: 'summary', 
            chart: 'line', 
            component: SummaryLines,
        }
    ]

    const findComponentAndDataInfo = (): {
        component: any, 
        data: SummaryDataForRealization[] | ParameterDataForRealization[],
        MBProcessed: number,
        timeSpentSeconds: number,
    } => {
        const { kind, chart } = spec
        const component = DataToComponentType.find((v) => v.kind === kind && v.chart === chart)!.component
        
        if (kind === 'parameter') {
            const param = getLoadedParameter(spec.query)!
            return { component, ...param }
        }
        
        if (kind === 'summary'){
            const summary = getLoadedSummary(spec.query)
            return { component, ...summary }
        }

        throw new TypeError(`Expected valid dataset kind, got ${kind}, expected one of [${['summary','parameter'].join(", ")}]`)
    }

    let containerWidth = 0
    let containerHeight = 0
    const onResize = (event: CustomEvent): void => {
        const { entries } = event.detail
        const entry = entries[0]
        const { width, height } = entry.contentRect;
        containerWidth = width;
        containerHeight = height;
    }
</script>

{#await Promise.all([ensureComponentDataIsLoaded(), ensureExperimentIsSelected()])}
    <p>Loading datasets...</p>
{:then}
    <div class="relative w-full h-full shadow-lg rounded m-5">
        <p class="p-2">{performanceInfo()}</p>
        <Sidebar>
            <div slot="controls">
                <Combobox
                    label="Ensembles"
                    placeholder="{'Select ensembles\u2026'}"
                    options={[{value: "ens1", label: "ensemble 1"}]}
                    multiselect
                />
                <TextInput
                    label="Realisations"
                    id="realisations"
                    placeholder="*"
                />
                <Combobox
                    label="Summary"
                    placeholder="{'Select summary keyword\u2026'}"
                    options={[{value: "ens1", label: "ensemble 1"}]}
                />
            </div>
        </Sidebar>
        <div   
            class="h-96 relative"
            use:watchResize
            on:resized={onResize}
        >
            {#if (containerWidth > 0 && containerHeight > 0)}
                <svelte:component 
                    this={findComponentAndDataInfo().component}
                    data={findComponentAndDataInfo().data}
                    style={computeEnsembleStyleMap()}
                    width={containerWidth-axisMarginLeft}
                    height={containerHeight-axisMarginBottom}
                />
            {/if}
        </div>
    </div>
{/await}
