import { ExperimentMetadata, type EnsembleParameterChartQuery, type EnsembleSummaryChartQuery, type Experiments, type ParameterChartInfos, type SummaryChartInfos } from "../types/chart_spec.d";
import { readable, type Readable } from 'svelte/store';

const urlParams = new URLSearchParams(window.location.search);
const serverURL = decodeURIComponent(urlParams.get("serverURL") || "http://localhost:8001")

// Just for dev
const lag = async (t: number): Promise<void> => new Promise(resolve => setTimeout(resolve, t))
const minLagMs = 0

const createQueryString = (obj: object): string => {
    const urlParams = new URLSearchParams()
    Object.entries(obj).forEach(([k, v]) => urlParams.append(k, v.toString()))
    return urlParams.toString()
}

const _summaryCache: Record<string, SummaryChartInfos> = {}
export const fetchSummary = async (query: EnsembleSummaryChartQuery): Promise<SummaryChartInfos> => { 
    await lag(minLagMs)
    const queryString = createQueryString(query)
    if (!(queryString in _summaryCache)) {
        const response = await fetch(`${serverURL}/api/summary_chart_data?${queryString}`)
        const responseJSON = await response.json()

        _summaryCache[queryString] = responseJSON as SummaryChartInfos
    }
    
    return _summaryCache[queryString]
}


export const getLoadedSummary = (query: EnsembleSummaryChartQuery): SummaryChartInfos => {
    const queryString = createQueryString(query)
    if (!(queryString in _summaryCache))
        throw new ReferenceError(
            `Expected summary to be loaded for: ${queryString}.`+
            `Loaded summaries: ${Object.keys(_summaryCache)}`
        )

    return _summaryCache[queryString]
}

let _experimentsMetadataCache: Experiments|undefined = undefined
export const fetchExperiments = async (forceRefresh=false): Promise<Experiments> => {
    await lag(minLagMs)
    if (!_experimentsMetadataCache || forceRefresh) {
        const response = await fetch(`${serverURL}/api/experiments`)
        const responseJSON = await response.json()
        const parsedResponseJSON: Experiments = {}
        Object.entries(responseJSON)
            .forEach(([k, v]) => parsedResponseJSON[k] = ExperimentMetadata.FromObject(v))

        _experimentsMetadataCache = parsedResponseJSON
    }

    return _experimentsMetadataCache
}

export const getLoadedExperiments = (): Experiments => _experimentsMetadataCache!

export const getLoadedExperimentsReadable = (): Readable<Experiments> => {
    return readable(getLoadedExperiments())
}

const _parametersCache: Record<string, ParameterChartInfos> = {}
export const fetchParameter = async (query: EnsembleParameterChartQuery): Promise<ParameterChartInfos> => {
    await lag(minLagMs)
    const queryString = createQueryString(query)
    if (!(queryString in _parametersCache)) {
        const response = await fetch(`${serverURL}/api/parameter_chart_data?${queryString}`)
        const responseJSON = await response.json()

        _parametersCache[queryString] = responseJSON as ParameterChartInfos
    }
    
    return _parametersCache[queryString]
}

export const getLoadedParameter = (query: EnsembleParameterChartQuery): ParameterChartInfos => {
    const queryString = createQueryString(query)
    if (!(queryString in _parametersCache))
        throw new ReferenceError(
            `Expected parameter to be loaded for: ${queryString}.`+
            `Loaded parameters: ${Object.keys(_parametersCache)}`
        )

    return _parametersCache[createQueryString(query)]
}
