<script lang="ts">
	import AreaLayer from './layers/AreaLayer.svelte'
	import type { ParameterDataForRealization } from "../../types/chart_spec";
    import SingleAxisLayer from "./layers/SingleAxisLayer.svelte";
    import * as d3 from "d3";

    // For summary we don't need a selector,
    // Maybe just pass a "*" or something like that
    export let axisMarginLeft: number = 35
    export let axisMarginBottom: number = 35
    export let showAxisX: boolean = true
    export let showAxisY: boolean = true
    export let data: ParameterDataForRealization[]
    export let style: Record<string, Record<string, string>>
    export let width: number
    export let height: number

    $: kdeCharts = (): { 
        lines: { 
            points: [number, number][], 
            domainX: [number, number], 
            domainY: [number, number],
            ensemble: string
        }[], 
        sharedDomainY: [number, number],
        sharedDomainX: [number, number] 
    } => {
        const kde = (
            ds: number[],
            bandwidth: number,
            kernelFunction: (u: number) => number,
            numPoints: number
        ): { kdeValues: [number, number][], domainY: [number, number], domainX: [number, number] } => {
            const domainX = d3.extent(ds)! as [number, number]
            const minX = domainX[0]!
            const maxX = domainX[1]!
            const spanX = maxX - minX
            const step = spanX / (numPoints - 1)
            
            // Generate evenly spaced x-values
            const xValues = Array.from({ length: numPoints }, (_, i) => minX + i * step)

            let yMin = Infinity
            let yMax = -Infinity

            const kdeValues: number[] = xValues.map((x: number) => {
                // Calculate the sum of kernel contributions from all data points
                const sum: number = ds.reduce((prev: number, current: number) => {
                    const diffN: number = (x - current) / bandwidth;
                    return prev + kernelFunction(diffN)
                }, 0)

                // Normalize by the bandwidth and number of data points
                return (1 / (ds.length * bandwidth)) * sum
            })

            const domainY = d3.extent(kdeValues)! as [number, number]

            yMin = Math.min(domainY[0], yMin)
            yMax = Math.max(domainY[1], yMax)

            return { 
                kdeValues: xValues.map((x, i) => [(x - minX) / spanX, kdeValues[i]]) as [number, number][],
                domainY,
                domainX
            }
        };


        const kdeEpanechnikov = (k: number) => ((v: number) => Math.abs(v /= k) <= 1 ? 0.75 * (1 - v * v) / k : 0)
        
        const bandwidth = 1.1
        const numPoints = 50
        const distinctEnsembles = [...new Set(data.map(d => d.ensemble_id).values()).values() ]
        const kdeInfosPerEnsemble = distinctEnsembles
            .map((ensemble) => ({
                ensemble,
                values: data
                    .filter(d => d.ensemble_id === ensemble)
                    .map(d => d.values)
            }))
            .map(({ensemble, values}) => ({
                ensemble,
                info: kde(values, bandwidth, kdeEpanechnikov(0.3), numPoints)
            }))
        
        const sharedDomainY = [
            Math.min(...kdeInfosPerEnsemble.map(d => d.info.domainY[0]))!, 
            Math.max(...kdeInfosPerEnsemble.map(d => d.info.domainY[1]))!
        ] as [number, number]
        
        const sharedDomainX = [
            Math.min(...kdeInfosPerEnsemble.map(d => d.info.domainX[0]))!, 
            Math.max(...kdeInfosPerEnsemble.map(d => d.info.domainX[1]))!
        ] as [number, number]

        const lines = kdeInfosPerEnsemble.map(d => ({
                points: d.info.kdeValues,
                domainX: d.info.domainX,
                domainY: d.info.domainY,
                ensemble: d.ensemble
            })
        )
        
        return {
            lines,
            sharedDomainY,
            sharedDomainX,
        }
    }
</script>

<svg class="absolute top-0 left-0 h-full w-full overflow-visible">
    {#if showAxisX || showAxisY}
        <g transform={`translate(${axisMarginLeft},0)`}>
            <SingleAxisLayer
                width={width-axisMarginLeft}
                height={height-axisMarginBottom}
                domainX={kdeCharts().sharedDomainX}
                domainY={kdeCharts().sharedDomainY}
                formatY={".0"}
                ticksY={14}
                ticksX={15}
                formatX={".1f"}
            />
        </g>
    {/if}
    <g transform={`translate(${axisMarginLeft},0)`}>
        {#each kdeCharts().lines as chart}
            <AreaLayer
                width={width-axisMarginLeft}
                height={height-axisMarginBottom}
                domainX={chart.domainX}
                domainY={chart.domainY}
                sharedDomainX={kdeCharts().sharedDomainX}
                sharedDomainY={kdeCharts().sharedDomainY}
                points={chart.points}
                style={style[chart.ensemble]}
                realization={chart.ensemble}
            />
        {/each}
    </g>
</svg>