<script lang="ts" context="module">
    import { tick } from "svelte"

    const MOUNTING_POINT = document.querySelector("#portal")

    export const portal = (element: HTMLElement) => {
        const update = async () => {
            if (!MOUNTING_POINT) {
                await tick()
            }

            MOUNTING_POINT?.appendChild(element)
        }

        const destroy = async () => {
            if (element.parentNode) {
                element.parentNode.removeChild(element)
            }
        }

        update()

        return { update, destroy }
    }
</script>

<div use:portal><slot /></div>
