export type ChartType = 'line' | 'area' | 'bars' | "kdeAreas"

export interface SummaryChartsSpec {
    kind: 'summary',
    chart: ChartType,
    query: ChartQuery,
    style?: Record<string, Record<string, string|number>>
}

export interface ParameterChartsSpec {
    kind: 'parameter'
    chart: 'kdeAreas'
    query: ChartQuery
    style?: Record<string, Record<string, string|number>>
}

export type ChartsSpec = ParameterChartsSpec | SummaryChartsSpec

