export interface ChartQuery {
    ensembles: string[]
    keyword: string
    experiment: string
}

export interface SummaryDataForRealization {
    [x: string]: any;
    ensemble: string
    ensembleAlias: string
    realization: string,
    domainX: [number, number],
    domainY: [number, number],
    points: [number, number][] // normalized
}

export interface ParameterDataForRealization {
    ensemble_id: string
    realization: string
    experiment_id: string
    values: number
    transformed_values: number
}

export interface ParameterChartInfos {
    MBProcessed: number,
    timeSpentSeconds: number,
    experiment: string,
    data: ParameterDataForRealization[]
}

export interface SummaryChartInfos {
    MBProcessed: number,
    timeSpentSeconds: number,
    experiment: string,
    axisX: string,
    axisY: string,
    timeAttrs: object,
    data: SummaryDataForRealization[]
}

 interface SummaryMetadata {
    name: string,
    inputFile: string
    keys: string[]
    reface: string[]
}

 interface ResponseMetadata {
    summary: SummaryMetadata
    // TODO add more once summary is rendered properly
}

interface TransferFunctionDefinition {
    name: string
    distribution: string
    args: number[]
}

interface ParametersMetadata {
    forward_init: boolean
    forward_init_file: null|string
    name: string
    template_file: string
    transfer_function_definitions: TransferFunctionDefinition[]
}

 interface EnsembleMetadata {
    id: string
    name: string
    iteration: int
    realizations: string[]
}

interface ExperimentMetadataObj {
    name: string
    id: string
    ensembles: Record<string, EnsembleMetadata>
    responses: ResponseMetadata
    parameters: Record<string, ParametersMetadata>
}

export class ExperimentMetadata implements ExperimentMetadataObj {
    name: string
    id: string
    ensembles: Record<string, EnsembleMetadata>
    responses: ResponseMetadata
    parameters: Record<string, ParametersMetadata>

    get numEnsembles() {
        return Object.keys(this.ensembles).length
    }

    constructor(
        name: string,
        id: string,
        ensembles: Record<string, EnsembleMetadata>,
        responses: ResponseMetadata,
        parameters: Record<string, ParametersMetadata>
    ) {
        this.name = name
        this.id = id
        this.ensembles = ensembles
        this.responses = responses
        this.parameters = parameters
    }

    public static FromObject(
        {   
            name,
            id,
            ensembles,
            responses,
            parameters
        }: ExperimentMetadataObj) {
        return new ExperimentMetadata(name, id, ensembles, responses, parameters)
    }

    public ensembleIdToAlias(ensembleId: string): string {
        const { iteration } = this.ensembles[ensembleId]
        return `ensemble:${iteration === 0 ? 'first' : iteration === (this.numEnsembles - 1) ? 'last' : iteration}`
    }

    public eachEnsemble(f: (ensembleId: string, ensembleAlias: string) => any) {
        Object.keys(this.ensembles).forEach((ensembleId) => {
            const ensembleAlias = this.ensembleIdToAlias(ensembleId)
            f(ensembleId, ensembleAlias)
        })
    }
}

export type Experiments = Record<string, ExperimentMetadata>
