<script lang="ts">
    import {
        createCombobox,
        melt,
        type ComboboxOptionProps,
    } from "@melt-ui/svelte";
    import Icon from "../icons/Icon.svelte";
    import Chip from "../Chip.svelte";
    import { createEventDispatcher } from "svelte";
    
    export let label: string = ""
    export let options: ComboboxOptionProps[] = []
    export let placeholder: string = ""
    export let multiselect: boolean = false
    export let value: any

    const dispatch = createEventDispatcher()

    const {
        elements: { menu, input, option, label: inputLabel },
        states: { open, inputValue, selected },
    } = createCombobox({
        forceVisible: true,
        multiple: multiselect,
        onSelectedChange: ({ next }) => {
            value = next
            dispatch("change", { value: next })
            return next
        },
        defaultSelected: value,
    });

    $: if (!multiselect && !$open) {
        $inputValue = ($selected as ComboboxOptionProps)?.label ?? '';
    }

    const onChipRemove = (e: CustomEvent) => {
        const { value } = e.detail.chip.dataset
        selected.update((opts) => {
            const idx = (opts as ComboboxOptionProps[]).findIndex(opt => opt.value === value)
            return (opts as ComboboxOptionProps[]).filter((_, i) => i !== idx)
        })
    }
</script>

<div class="flex flex-col gap-1 combobox">
    <!-- svelte-ignore a11y-label-has-associated-control -->
    <label use:melt={$inputLabel} class="combobox__label">
        {label}
    </label>

    <div class="relative">
        <input
            use:melt={$input}
            class="min-h-10 px-3 w-full combobox__field"
            placeholder="{placeholder}"
        />
        <div class="absolute right-2 top-1/2 z-10 -translate-y-1/2">
            {#if $open}
                <Icon name="arrow_drop_up" />
            {:else}
                <Icon name="arrow_drop_down" />
            {/if}
        </div>
    </div>

    {#if multiselect && Array.isArray($selected) && $selected.length}
        <div class="flex gap-1">
            {#each $selected as opt}
                <Chip label="{opt.label}" data-value={opt.value} on:delete={onChipRemove} />
            {/each}
        </div>
    {/if}
</div>

{#if $open}
    <ul class="flex flex-col p-2 max-h-72 gap-1 combobox__option-list" use:melt={$menu}>
        {#each options as o, index (index)}
            <li class="flex items-center relative px-3 scroll-my-2 min-h-10 combobox__option" use:melt={$option(o)}>
                <!-- {#if $isSelected(manga)} -->
                <!--   <div class="check absolute left-2 top-1/2 z-10 "> -->
                <!--     <Check class="square-4" /> -->
                <!--   </div> -->
                <!-- {/if} -->
                <span>{o.label}</span>
            </li>
        {:else}
            <li class="relative cursor-pointer rounded-md py-1 pl-8 pr-4">
                No results found
            </li>
        {/each}
    </ul>
{/if}

<style lang="scss">
    .combobox__label {
        font-size: 12px;
        color: #6F6F6F;
    }

    .combobox__field {
        background-color: #F7F7F7;
        box-shadow: 0px 1px 0px #3D3D3D;

        &:focus {
            box-shadow: 0px 0px 0px 2px #007079;
            outline: none;
        }

        &::placeholder {
            color: #6F6F6F;
        }
    }

    .combobox__option-list {
        background-color: #fff;
        box-shadow: 0px 3px 4px 0px rgba(0, 0, 0, 0.12), 0px 2px 4px 0px rgba(0, 0, 0, 0.14);
        border-radius: 4px;
        z-index: 10;
        overflow-y: auto;
    }
    
    .combobox__option {
        cursor: pointer;
        border-radius: 4px;

        &:hover, &[data-highlighted] {
            background-color: #DEEDEE;
        }

        &[data-selected] {
            background-color: #C3F3D2;
            color: #004F55;

            &:hover, &[data-highlighted] {
                background-color: #E6FAEC;
            }
        }
    }
</style>
