<script lang="ts">
    import type { ComboboxOptionProps } from "@melt-ui/svelte";
    import { portal } from "./Portal.svelte";
    import Combobox from "./input/Combobox.svelte";

    export let experiments: ComboboxOptionProps[] = [
        { label: "Snaik oil", value: "asdaskjd-fdsf-sdfdsfsd" },
        { label: "Dogran", value: "dsafsdfds-fdsf-sdfdsfsd" },
        { label: "Dogron ESMDA", value: "gjdnfksdnk-fdsf-sdfdsfsd" },
    ]
    
    export let experiment = false

    let open = false
    const backdropCloseListener = (e: KeyboardEvent) => {
        if (e.code === "Escape") {
            open = false
            window.removeEventListener("keypress", backdropCloseListener)
        }
    }

    const toggleOpen = () => {
        if (!open) 
            window.addEventListener("keypress", backdropCloseListener)
        else 
            window.removeEventListener("keypress", backdropCloseListener)
        
        open = !open
    }    
</script>

<div>
    <slot name="open-button">
        <button
            class="h-10 w-20 bg-red"
            on:click={toggleOpen}
            aria-pressed={open}
            >{open}
        </button>
    </slot>
    <div use:portal>
        <div
            class={["fixed top-0 left-0 h-dvh w-dvw sidebar-backdrop", open && "sidebar-backdrop--visible"].filter(Boolean).join(" ")}
            on:click={toggleOpen}
        ></div>

        <nav class={["fixed flex top-0 left-0 h-dvh sidebar", open && "sidebar--open"].filter(Boolean).join(" ")}>
            <ul class="p-2 min-w-60 sidebar__experiments">
                <Combobox
                    label="Experiment"
                    placeholder="{'Select experiment\u2026'}"
                    options={experiments}
                    bind:value={experiments[0]}
            />
            </ul>
            <slot name="controls">
                <div class="flex flex-col gap-4 px-3 min-w-96">
                    Add controls to the \"controls\" slot
                </div>
            </slot>
        </nav>
    </div>
</div>

<style lang="scss">
    .sidebar-backdrop {
        background-color: transparentize($color: #000000, $amount: 0.75);
        visibility: hidden;
        opacity: 0;
        transition: all ease 250ms;

        &--visible {
            visibility: visible;
            opacity: 1;
        }
    }

    .sidebar {
        background-color: white;
        z-index: 1;
        transform: translateX(-100%);
        transition: transform ease 400ms;
    }

    .sidebar--open {
        transform: translateX(0);
    }

    .sidebar__experiments {
        border-right: 1px solid lightgray;

        a {
            border-radius: 4px;

            &:hover {
                background-color: #DEEDEE;
            }
        }
    }
</style>
